@import 'partials/fonts';
@import 'partials/variables';
@import 'partials/mixins';
@import '../libs/slick/slick/slick.scss';
// @import '../../../build/assets/i/svg/bg/css/sprite.css'
body {
	margin: 0;
	padding: 0;
	font-family: 'Pt Sans', 'Arial', sans-serif;
	font-size: 16px;
	line-height: 1.4;
	color: $white;
	background: $black;
}

* {
	box-sizing: border-box;
}

.title {
	font-weight: normal;
	font-size: 30px;
	@media screen and (max-width: 560px) {
		font-size: 22px;
	}

	&.title--color {
		color: $lochmara;
	}

}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
	color: $lochmara;
}

img {
	max-width: 100%;
	height: auto;
}

ul {
	@include reset-list;
}

p {
	margin: 0;
	padding: 0;
}

.hidden {
	display: none;
}

.container {
	margin: 0 auto;
	padding: 0 45px;
	max-width: 1920px;
	@media screen and (max-width: 768px) {
		padding-left: 15px;
		padding-right: 15px;
	}

}

.main-header__wrap {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100px;
}

.main-menu {
	display: flex;
	justify-content: center;
	align-items: center;
	@media screen and (max-width: 1200px) {
		position: absolute;
		top: 20px;
		display: none;
		margin-top: 100px;
		width: 100%;
		text-align: center;
		background: $black;
		z-index: 1000;
	}

	a {
		display: block;
		padding: 0 32px;
		color: $gray;
		transition: all .3s;
		@media screen and (max-width: 1180px) {
			padding: 16px;
		}

		&:hover {
			color: white;
		}
	}

	li:nth-child(3) {
		margin-right: 110px;
		@media screen and (max-width: 1180px) {
			margin-right: 0;
		}

	}

	li:nth-child(4) {
		margin-left: 110px;
		@media screen and (max-width: 1180px) {
			margin-left: 0;
		}
	}

	li.current-menu-item {
		a {
			color: $white;
		}
	}
}

.logo {
	position: absolute;
	top: 18px;
	left: 52%;
	transform: translateX(-50%);
	display: block;
	z-index: 10000;
	@media screen and (max-width: 1180px) {
		left: 20px;
		transform: translateX(0);
	}
	@media screen and (max-width: 500px) {
		left: 0;
	}
}

.header-socials {
	position: absolute;
	top: 38px;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	@media screen and (max-width: 500px) {
		display: none;
	}

	a {
		display: block;
		margin-left: 40px;
		transition: all .3s;
		opacity: .8;

		&:hover {
			opacity: 1;
		}

		i {
			font-size: 20px;
			color: $lochmara;
		}
	}
}

.link {
	color: $lochmara;
	transition: all .3s;
	transform: scale(1);
	&:hover {
		color: lighten($lochmara, 60%);
		transform: scale(1.004);
	}
	&:hover i{
		color: lighten($lochmara, 60%);
	}
	&.link--reverse {
		display: flex;
		align-items: center;
		span {
			order: 1;
			margin-right: 0;
			margin-left: 8px;
		}
		i {
			transition: all .3s;
		}
	}

	span {
		display: inline-block;
		margin-right: 10px;
	}

}

.main-slider {
	margin-bottom: 50px;

	&__item {
		position: relative;
		height: 900px;
		@media screen and (max-width: 1600px) {
			padding: 0 30px;
			height: 600px;
			background-size: cover;
			background-position: center;
		}
		@media screen and (max-width: 600px) {
			height: 400px;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
			@media screen and (max-width: 768px) {
				background: rgba(0, 0, 0, .6);
			}

		}
	}

	&__content-wrap {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin: 0 auto;
		max-width: 1444px;
		height: 100%;
		z-index: 2;
	}

	&__title {
		font-size: 30px;
	}

	&__text {
		margin-bottom: 64px;
		font-size: 30px;
		line-height: 1.1;
		@media screen and (max-width: 600px) {
			margin-bottom: 20px;
			font-size: 22px;
		}

	}
}

.sandwitch-wrap {
	position: absolute;
	top: 20px;
	right: 100px;
	display: none;
	padding: 18px 10px;
	//border: 1px solid $lochmara;
	border-radius: 3px;
	z-index: 1001;
	@media screen and (max-width: 1200px) {
		top: 25px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	@media screen and (max-width: 500px) {
		right: 0;
	}

}

.sandwitch {
	position: relative;
	display: block;
	width: 23px;
	height: 14px;
	cursor: pointer;
}

.sandwitch--active .sandwitch__line--top {
	top: 6px;
	transform: rotate(45deg);
}

.sandwitch--active .sandwitch__line--middle {
	opacity: 0;
}

.sandwitch--active .sandwitch__line--bottom {
	top: 6px;
	transform: rotate(-45deg);
}

.sandwitch__line {
	position: absolute;
	left: 0;
	display: block;
	width: 100%;
	height: 2px;
	transition: all linear 0.1s;
	background-color: $lochmara;
}

.sandwitch__line--top {
	top: 0;
}

.sandwitch__line--middle {
	top: 6px;
}

.sandwitch__line--bottom {
	top: 12px;
}

.intro {
	display: flex;
	margin-bottom: 50px;
	@include border;
	@media screen and (max-width: 1300px) {
		display: table;
		margin: 0 auto 50px;
		max-width: 800px;
	}

	.title {
		margin-bottom: 20px;
	}

	&__text,
	&__img {
		flex: 1;
	}

	img {
		display: block;
	}

	&__text {
		display: flex;
		flex-direction: column;
		padding: 80px 80px 50px 40px;
		background-size: 260px;
		background-position: center bottom;
		background-repeat: no-repeat;
		@media screen and (max-width: 1700px) {
			background-size: 20%;
		}
		@media screen and (max-width: 1440px) {
			padding-top: 40px;
		}
		@media screen and (max-width: 1400px) {
			background-size: 100px;
		}
		@media screen and (max-width: 1300px) {
			margin-bottom: 50px;
			height: 400px;
			background-size: 180px;
		}
		@media screen and (max-width: 820px) {
			background-size: 20%;
		}
		@media screen and (max-width: 700px) {
			padding: 40px;
			height: auto;
			background-image: none !important;
		}

		p {
			text-align: justify;
			@media screen and (max-width: 1300px) {
				margin-bottom: 30px;
			}

		}

		.link {
			margin-top: auto;
		}
	}
}

.categories-block {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 30px;
	margin-bottom: 50px;
	@media screen and (max-width: 1660px) {
		margin: 0 auto 50px;
		max-width: 1100px;
		grid-template-columns: repeat(2, 1fr);
	}
	@media screen and (max-width: 768px) {
		display: block;
		max-width: 520px;
	}

	&__item {
		position: relative;
		height: 500px;
		@include border;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		@media screen and (max-width: 768px) {
			margin-bottom: 30px;
		}
		@media screen and (max-width: 400px) {
			height: 300px;
		}


	}

	&__footer {
		position: absolute;
		left: 24px;
		bottom: 24px;
	}
}

.services-block {
	margin-bottom: 50px;

	&__title {
		margin-bottom: 20px;
	}

	&__wrap {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30px;
		@media screen and (max-width: 768px) {
			display: block;
			margin: 0 auto 50px;
			max-width: 520px;
		}

	}

	&__item {
		position: relative;
		height: 580px;
		@include border;
		@media screen and (max-width: 768px) {
			margin-bottom: 30px;
		}

		.title {
			text-align: left;
		}
	}

	&__footer {
		position: absolute;
		bottom: 40px;
		left: 40px;
		right: 40px;
		padding-bottom: 30px;
		@include border-bottom-dashed;
	}

	&__text {
		max-width: 580px;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;

		li {
			position: relative;
			margin-right: 20px;
			padding-left: 12px;

			&::before {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background: $white;
			}
		}
	}
}

.main-footer {
	&__content {
		display: grid;
		grid-template-columns: 1fr 570px;
		grid-gap: 30px;
		@media screen and (max-width: 1830px) {
			display: block;
			margin: 0 auto;
			max-width: 1100px;
		}

	}

	&__info {
		.title {
			margin-bottom: 56px;
		}

		&-wrap {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 20px;
			margin-bottom: 50px;
			padding: 80px 50px 50px;
			@include border;
			@media screen and (max-width: 1180px) {
				display: block;
			}
		}

		&-item {
			&:first-child {
				@media screen and (max-width: 1180px) {
					margin-bottom: 30px;
				}
			}
		}

		i {
			margin-right: 40px;
			font-size: 30px;
		}

		.fab.fa-instagram {
			font-size: 34px;
		}
	}

	&__contacts {
		display: grid;
		grid-template-columns: 320px 1fr;
		grid-gap: 20px;
		@media screen and (max-width: 1830px) {
			grid-template-columns: 300px 1fr;
		}
		@media screen and (max-width: 620px) {
			display: block;
		}

		a {
			color: #008BCF;
		}

		&__item {
			span {
				display: block;
			}

			@media screen and (max-width: 620px) {
				display: block;
				margin-bottom: 30px;
			}
		}
	}

	&__partners {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media screen and (max-width: 1180px) {
			justify-content: flex-start;
		}

		&-item {
			display: block;
			@media screen and (max-width: 1180px) {
				margin-right: 20px;
			}
			@media screen and (max-width: 530px) {
				margin-bottom: 20px;
			}
		}

		@media screen and (max-width: 530px) {
			display: block;
			text-align: center;
		}

	}

	.map {
		@include border;
		@media screen and (max-width: 1830px) {
			margin-bottom: 50px;
		}
		iframe {
			display: block;
		}
	}

	&__form {
		padding: 80px 50px 26px;
		@include border;

		.title {
			margin-bottom: 45px;
		}

		.form-group {
			margin-bottom: 35px;

			&:last-of-type {
				display: flex;
				align-items: center;
				@media screen and (max-width: 500px) {
					display: block;
				}

			}

			label {
				display: block;
				margin-bottom: 30px;
				font-size: 12px;
				letter-spacing: .1px;
				text-transform: uppercase;
			}

			input[type="text"],
			textarea,
			input[type="email"] {
				width: 100%;
				height: 40px;
				line-height: 40px;
				background: transparent;
				border: none;
				border-bottom: 1px dashed $white;
			}

			input[type="submit"] {
				margin-right: 24px;
				width: 144px;
				height: 69px;
				font-size: 18px;
				line-height: 69px;
				text-align: center;
				color: $black;
				background: $lochmara;
				border: none;
				cursor: pointer;
				transition: all .3s;
				@media screen and (max-width: 500px) {
					display: block;
					margin-bottom: 20px;
				}

				&:hover {
					background: darken($lochmara, 10%);
				}
			}
		}
	}
}

.checkbox {
	position: absolute;
	z-index: -1;
	opacity: 0;
	margin: 10px 0 0 20px;
}

.main-footer__form .form-group .checkbox + label {
	position: relative;
	cursor: pointer;
	display: inline-block;
	margin-bottom: 0;
	padding-left: 30px;
	vertical-align: middle;
	text-transform: none;
}

.checkbox + label:before {
	content: '';
	position: absolute;
	top: 7px;
	left: 0;
	margin-right: 16px;
	width: 20px;
	height: 20px;
	display: inline-block;
	border: 1px solid $lochmara;
	border-radius: 5px;
	transition: .2s;
	@media screen and (max-width: 1830px) {
		top: -3px;
	}
	@media screen and (max-width: 830px) {
		top: 7px;
	}

}

.checkbox + label:after {
	content: '\f00c';
	position: absolute;
	font-family: "Font Awesome 5 Pro";
	top: 7px;
	left: 3px;
	width: 22px;
	height: 22px;
	font-size: 16px;
	color: white;
	transition: .2s;
	opacity: 0;
	@media screen and (max-width: 1830px) {
		top: -3px;
	}
	@media screen and (max-width: 830px) {
		top: 7px;
	}
}

.checkbox:checked + label:after {
	opacity: 1;
}

.copyright {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 30px 0 50px;
	font-size: 14px;
	color: $gray;
	@media screen and (max-width: 820px) {
		display: block;
	}

	&__item {
		display: flex;

		&:first-child {
			margin-bottom: 20px;
		}

		img {
			margin-left: 20px;
		}
	}

	a {
		color: $lochmara;
	}
}

.products {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-column-gap: 30px;
	grid-row-gap: 60px;
	padding: 80px 0;
	@media screen and (max-width: 1840px) {
		grid-template-columns: repeat(3, 1fr);
		margin: 0 auto;
		max-width: 1300px;
	}
	@media screen and (max-width: 1400px) {
		grid-template-columns: repeat(2, 1fr);
		max-width: 856px;
	}

	@media screen and (max-width: 946px) {
		display: block;
		max-width: 414px;
	}
}
.products {
	.product__item {
		@media screen and (max-width: 946px) {
			margin-bottom: 50px;
		}
	}
	.product__img {
		overflow: hidden;
	}
}

.product {
	@media screen and (max-width: 576px) {
		margin-bottom: 50px;
	}
	&__img {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 20px;
		@include border;
		height: 413px;
		@media screen and (max-width: 446px) {
			height: 290px;
			overflow: hidden;
		}
		img {
			display: block;
		}
	}
}

.alte-bevande {
	display: grid;
	grid-template-columns: 420px 1fr;
	grid-gap: 40px;
	padding: 80px 0;
	@media screen and (max-width: 1200px) {
		display: block;
	}

	&__list {
		@media screen and (max-width: 1200px) {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			margin-bottom: 50px;
		}
		@media screen and (max-width: 1100px) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media screen and (max-width: 430px) {
			display: block;
		}


		li {
			position: relative;
			transition: all .3s;
			cursor: pointer;

			&.active {
				.alte-bevande__img {
					box-shadow:0px 0px 0px 2px $lochmara inset;
					&::after {
						background: $lochmara;
					}
				}

			}
			.alte-bevande__img {
				position: relative;
				box-shadow:0px 0px 0px 2px transparent inset;
				height: 190px;
				background-size: cover;
				background-position: top center;
				transition: .3s;
				&:hover {
					box-shadow:0px 0px 0px 2px $lochmara inset;
				}
				&:hover::after {
					background: $lochmara;
				}
				&::after {
					content: '';
					position: absolute;
					right: 0;
					top: 0;
					width: 10px;
					height: 100%;
					background: transparent;
					transition: .3s;
				}
			}
			img {
				display: block;
			}
		}

		.title {
			position: absolute;
			bottom: 10px;
			left: 10px;
			transition: all .3s;
		}
	}
	&__content {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 30px;
		@media screen and (max-width: 1400px) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media screen and (max-width: 1200px) {
			grid-template-columns: repeat(4, 1fr);
		}
		@media screen and (max-width: 1100px) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media screen and (max-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media screen and (max-width: 430px) {
			display: block;
		}

		.alte-bevande__img {
			@include border;
		}
	}
	&__item {
		display: block;
		@media screen and (max-width: 430px) {
			margin-bottom: 40px;
		}

		.link {
			display: block;
			font-size: 16px;
		}
	}
	&__img {
		img {
			display: block;
		}
	}
}
.single-product {
	display: flex;
	padding: 100px 0 80px;
	@media screen and (max-width: 1100px) {
		display: block;
	}


	&__image {
		margin-right: 15px;
		text-align: center;
		width: 550px;
		@media screen and (max-width: 1100px) {
			margin-bottom: 30px;
			width: auto;
			text-align: left;
		}

	}
	&__content {
		&-wrap {
			display: flex;
			margin-bottom: 62px;
			@media screen and (max-width: 1380px) {
				display: block;
			}
		}
	}
	&__title {
		margin-bottom: 30px;
		font-size: 48px;
		line-height: 1;
		font-weight: normal;
		@media screen and (max-width: 768px) {
			font-size: 30px;
		}

	}
	&__text {
		flex: 0 0 834px;
		margin-right: 50px;
		@media screen and (max-width: 1380px) {
			margin-bottom: 30px;
		}

	}
	&__data {
		padding: 54px;
		max-width: 850px;
		@include border;
		@media screen and (max-width: 470px) {
			padding: 30px;
		}

	}
	&__link {
		display: block;
		position: relative;
		padding-left: 22px;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 1px;
			height: 61px;
			background: $lochmara;
		}
	}
	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		@media screen and (max-width: 900px) {
			display: block;
		}

		a {
			line-height: 2.4;
			@media screen and (max-width: 900px) {
				margin-bottom: 20px;
				line-height: 1.4;
			}
			@media screen and (max-width: 470px) {
				font-size: 12px;
			}

		}
	}
}
.section {
	&__intro {
		position: relative;
		margin-bottom: 33px;
		height: 730px;
		background-size: cover;
		background-position: left center;
		background-repeat: no-repeat;
		@media screen and (max-width: 968px) {
			height: 400px;
		}

	}
	&__title {
		position: absolute;
		bottom: 60px;
		left: 85px;
		font-size: 30px;
		font-weight: bold;
		@media screen and (max-width: 601px) {
			left: 30px;
		}

	}
}
.about {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 34px;
		margin-bottom: 50px;
		@media screen and (max-width: 1300px) {
			display: block;
			margin: 0 auto 50px;
			max-width: 1100px;
		}

		.title {
			margin-bottom: 16px;
			font-weight: bold;
		}
	}
	&__item {
		&:first-child {
			order: 2;
		}
	}
	&__small-item,
	&__big-item {
		position: relative;
		height: 100%;
		@include border;
	}
	&__small-item {
		padding: 34px;
		&:first-child {
			margin-bottom: 34px;
		}
		p {
			margin-bottom: 10px;
		}
	}
	&__big-item {
		position: relative;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@media screen and (max-width: 1300px) {
			margin-bottom: 34px;
			height: 400px;
		}

		&-content {
			position: absolute;
			bottom: 34px;
			left: 34px;
			right: 34px;
		}
	}
}
.contacts {
	padding: 30px 0;
}
